'use strict'; // MODULES //

var isArray = require('validate.io-array'); // IS STRING ARRAY //

/**
* FUNCTION: isStringArray( value )
*	Validates if a value is an array of string primitives.
*
* @param {*} value - value to be validated
* @returns {Boolean} boolean indicating if a value is an array of string primitives
*/


function isStringArray(value) {
  var len;

  if (!isArray(value)) {
    return false;
  }

  len = value.length;

  if (!len) {
    return false;
  }

  for (var i = 0; i < len; i++) {
    if (typeof value[i] !== 'string') {
      return false;
    }
  }

  return true;
} // end FUNCTION isStringArray()
// EXPORTS //


module.exports = isStringArray;